import { useParams } from "react-router-dom/cjs/react-router-dom.min";
import useFetch from './useFetch';
import Spinner from './Spinner'

const BlogDetails = () => {
    const {id} = useParams();
    const { data, isLoading, error } = useFetch(`https://jsonplaceholder.typicode.com/comments/${id}`);
    
    return ( 
        <div className="blog-details">
            { isLoading && <Spinner/> }
            { error && <div>{ error }</div>}
            { data && (
                <article>
                    <h2>{data.name}</h2>
                    <p style={{fontWeight : '700'}}>Posted by {data.email}</p>
                    <div>{data.body}</div>
                </article>
            ) }
        </div>
     );
}
 
export default BlogDetails;