import { useState } from "react";
import Spinner from './Spinner';

const Create = () => {

    const [title    , setTitle]     = useState('');
    const [body     , setBody]   = useState('');
    const [author   , setAuthor]    = useState('Budimir');

    const [isLoading, setIsLoading] = useState(false);
    const [response , setResponse]  = useState('');
    const [error    , setError]     = useState('');
    const [blogSent , setBlogSent]  = useState(false);



    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(e);
        setIsLoading(true);
        const blog = {title, body, author};
        setTimeout(() => {
            fetch('https://jsonplaceholder.typicode.com/comments', {
                method: 'POST',
                headers: {'Content-type': 'application/json; charset=UTF-8'},
                body : JSON.stringify(blog)
            })
            .then((res) => {
                if(!res.ok){
                    throw Error('Could not fetch the data !');
                }
                return res.json();
            })
            .then((res) => {
                setTitle('');
                setBlogSent(true);
                setResponse(res);
                setIsLoading(false)
                setBlogSent(true);
            })
            .catch(err => {
                setError(err.message);
                setIsLoading(false);
            })
        }, 1500)
    } 

    return ( 
        
        <div className="create">
            {isLoading && <div><Spinner /> Please wait...</div>}
            {blogSent && <div>Success !!! <br/> Blog list updated</div>}
            {(!blogSent && !isLoading) && 
            <div>
            <h2>Add a New Blog</h2>
            <form onSubmit={handleSubmit}>
                <label>Blog Title</label>
                <input 
                type="text"
                required 
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                />
                <label>Blog content</label>
                <textarea
                style={{minHeight : '10rem', margin : '0, 1rem'}}
                required
                value={body}
                onChange={(e) => setBody(e.target.value)}
                >
                </textarea>
                <label>Blog author</label>
                <select 
                value={author}
                onChange={(e) => setAuthor(e.target.value)}
                >
                    <option value="Budimir">Budimir</option>
                    <option value="Ivan">Ivan</option>
                    <option value="Aleksandar">Aleksandar</option>
                </select>
                <button type="Submit">Add Blog</button>
            </form>
            </div>
            }
        </div>
     );
}
 
export default Create;